@import url(https://fonts.googleapis.com/css?family=Share+Tech+Mono);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Header {
    position: fixed;
    top: 0;
    z-index: 5;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    width: 100%;
    background-color: #B7808B;
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.1);
    padding-top: 10px;
    padding-bottom: 10px;
}

.logo {
    max-width: 80vw;
}
.home {
	display: flex;
	min-height: 100vh;
	justify-content: center;
  	background-color: #6B6477;
}

.body {
	width: 100%;
	max-width: 1366px;
	display: flex;
	flex-direction: column;
	padding: 80px;
}

.banner {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 300px;
	font-family: 'Share Tech Mono';
	font-size: 60px;
	text-align: center;
	color: white;
}

.guides-list {
	width: 100%;
	font-family: 'Share Tech Mono';
	color: white;
}

.guide-card {
	position: relative;
	width: 90%;
	max-width: 1366px;
	overflow: hidden;
	padding: 30px;
}

.guide-card-title {
	font-size: 30px;
	margin-bottom: 20px;
}

.guide-card-description {
	font-size: 17px;
}

.guide-card-tags {
	font-size: 15px;
	color: rgba(256, 256, 256, 0.6);
	margin-top: 20px;
}

@media (max-width: 600px) {
	.body {
		padding: 20px;
	}

	.banner {
		font-size: 40px;
	}

	.guide-card {
		padding: 20px;
	}
}

a:link {
	color: #FFCDB3;
	text-decoration: none;
}

a:visited {
	color: #FFCDB3;
	text-decoration: none;
}

a:hover{
	color: #E6989B;
	text-decoration: underline;
}

.page {
	display: flex;
	min-height: 100vh;
	justify-content: center;
  	background-color: #6B6477;
}

.body {
	width: 100%;
	max-width: 1366px;
	display: flex;
	flex-direction: column;
	padding: 80px;
}

.banner {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 300px;
	font-family: 'Share Tech Mono';
	font-size: 60px;
	color: white;
}

.content {
    max-width: 100%;
	font-family: 'Share Tech Mono';
	color: white;
    font-size: 17px;
    padding: 50px;
}

.step {
    font-size: 25px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.image {
    margin: 20px;
	max-width: 90%;
}

.jump {
	cursor: pointer;
	font-size: 30px;
	color: #FFCDB3;
	text-align: center;
	margin-top: 50px;
	margin-bottom: 50px;
}

.divider {
	height: 3px;
	width: 80%;
	background-color: #E6989B;
	margin: 10%;
}

@media (max-width: 600px) {
	.body {
		padding: 20px;
	}

	.banner {
		font-size: 40px;
	}

	.content {
		padding: 20px;
	}
}
.navigation-wrapper {
	position: relative;
	width: 100%;
	font-family: 'Share Tech Mono';
	font-size: 15px;
	color: #FFCDB3;
}

.back {
	display: inline-block;
	max-width: 45%;
}

.top {
	cursor: pointer;
	position: absolute;
	top: 0;
	right: 0;
	max-width: 45%;
}

